<template>
  <v-container>

    <v-row class="text-center">
      <v-col cols="12" class="d-flex message-block">
        <v-card
            elevation="5"
            max-width="900"
            min-height="550"
            width="100%"
            class="ma-auto pa-5"

        >

          <v-card
              class="d-flex flex-column"
              elevation="0"
          >
            <v-list-item
                class="pr-0"
                dense
            >
              <v-list-item-avatar
                  class="mb-0"
                  left
                  color="#C4C4C4"
                  size="36"
              >
                AA
              </v-list-item-avatar>
              <v-textarea
                  class="mt-2"
                  rows="1"
                  placeholder="Добавить комментарий..."
                  outlined
                  dense
                  hide-details
                  no-resize
                  auto-grow
                  wrap="hard"
                  maxlength="3000"
                  v-model.trim="commentObj.comment_text"
                  @input="delSlash"
              >

              </v-textarea>
            </v-list-item>

            <v-row class="ma-0 mt-3 align-center justify-space-between">

              <div class="ml-15">
                <v-btn
                    class="mr-2"
                    icon
                    color="#B3B3B3"
                >
                  <v-icon dark size="28">mdi-paperclip</v-icon>
                </v-btn>

                <v-btn
                    icon
                    color="#B3B3B3"
                >
                  <v-icon size="28" dark>mdi-emoticon-happy-outline</v-icon>
                </v-btn>


              </div>


              <v-btn
                  class="white--text text-capitalize text-body-1"
                  elevation="0"
                  color="#6750A4"
                  font-size="1.2em"
                  @click.stop="sendComment"
              >
                Отправить
              </v-btn>
            </v-row>


          </v-card>

          <v-card
              width="100%"
              height="410"
              elevation="0"
              class="overflow-y-auto mt-6"
          >
            <template
                v-for="comment in comments"
            >

              <v-card
                  :key="comment.id"
                  elevation="0"
              >
                <v-list-item three-line class="pr-0">
                  <v-list-item-avatar
                      left
                      color="#C4C4C4"
                      size="36"
                  >
                    AA
                  </v-list-item-avatar>
                  <v-list-item-content>

                    <div class="text-subtitle-2 text-left mb-1">
                      <span :style="getRandomColor(comment.id)">{{ comment.id }}</span>
                      <span> {{ formatDate(comment.dateCreated) }}</span>
                    </div>

                    <template>
                      <div class="text-justify mb-0 comment-text">
                        {{ comment.commentText }}
                      </div>
                    </template>
                  </v-list-item-content>

                </v-list-item>

                <v-card-actions class="pa-0 ml-15">
                  <v-btn
                      text
                      color="#000000"
                      class="text-capitalize text-body-1 font-weight-bold"
                  >
                    Ответить
                  </v-btn>

                  <v-btn
                      text
                      color="rgba(0,0,0,.7)"
                      class="text-capitalize text-body-1  ml-0"
                  >
                    Изменить
                  </v-btn>

                  <v-btn
                      text
                      color="rgba(0,0,0,.7)"
                      class="text-capitalize text-body-1 ml-0"
                  >
                    Удалить
                  </v-btn>
                </v-card-actions>

              </v-card>

            </template>

          </v-card>

        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import {mapActions, mapGetters} from "vuex";

export default {
  name: "Comments",

  data() {
    return {
      commentObj: {
        path: "",
        page_id: "ЖКХ/t573858493",
        comment_text: "",
        is_private: true,
        attachment: [],
        user_id: "78",
      },

      colors: ["#0038FF", "#FE0000", "#28A015"],
    };
  },

  methods: {
    ...mapActions([
      "fetchComments",
      "SET_COMMENT",
      "SET_RANDOM_PATH",
    ]),

    /**
     * Метод отправки коментария
     */
    async sendComment() {
      if (this.commentObj.comment_text !== "") {
        this.commentObj.path = String(this.path);
        let regexp = /"(\\.|[^"\\])*"/g;
        this.commentObj.comment_text.match(regexp);
        await this.SET_COMMENT(this.commentObj);

        this.commentObj = {
          path: "",
          page_id: "ЖКХ/t573858493",
          comment_text: "",
          is_private: true,
          attachment: [],
          user_id: "78",
        };
      }
    },

    /**
     * Метод форматирования даты
     * @param date
     * @returns {string}
     */
    formatDate(date) {
      let newDate = new Date(date);
      const options = {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
        hour: "2-digit",
        minute: "2-digit",
        second: "2-digit",
      };
      return new Intl.DateTimeFormat("ru-Ru", options).format(newDate);
    },

    /**
     * Метод установления рандомного цвета для имени пользователя
     * @returns {{color: string}}
     */

    getRandomColor() {
      return {
        color: this.colors[Math.floor(Math.random() * this.colors.length)],
      };
    },

    /**
     * Метод удаления символа "/"
     */
    delSlash() {
      this.commentObj.comment_text = this.commentObj.comment_text.replace(/\\|\//g, "");
    },
  },

  computed: {
    ...mapGetters({
      comments: "COMMENTS",
      path: "PATH",
    }),


  },

  mounted() {
    this.SET_RANDOM_PATH();
    this.fetchComments("ЖКХ/t573858493");
  },

};
</script>

