import Vue from "vue";
import Vuex from "vuex";
import api from "@/utils/api";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    comments: [],
    randomPath: null,
  },
  mutations: {
    GET_COMMENTS(state, comments) {
      Vue.set(state, "comments", comments);
    },

    CHANGE_RANDOM_PATH(state, path) {
      Vue.set(state, "randomPath", path);
    },

  },
  actions: {
    async fetchComments({commit}, payload) {
      try {
        const {data} = await api.get("/v1/comments", {params: {page_id: payload}});
        await commit("GET_COMMENTS", data);
        return data;
      } catch (e) {
        console.log(e);
      }
    },

    async SET_COMMENT({dispatch, commit}, payload) {
      try {
        await api.post("/v1/comment", payload);
        await commit("CHANGE_RANDOM_PATH", Math.ceil(Math.random() * (999999999 - 0o00000001) + 0o00000001));
        dispatch("fetchComments", "ЖКХ/t573858493");
      } catch (e) {
        console.log(e);
      }
    },

    SET_RANDOM_PATH({commit}) {
      commit("CHANGE_RANDOM_PATH", Math.ceil(Math.random() * (999999999 - 0o00000001) + 0o00000001));
    },
  },
  getters: {
    COMMENTS: state => state.comments,
    PATH: state => state.randomPath,
  },
});
